.extra-services-block {
	width: 100%;
	overflow-x: auto;
	overflow-y: hidden;
	margin-bottom: 40px;

	&.services-plan {
		&::-webkit-scrollbar {
			width: 0;
		}

		table {

			.plans-cost {
				td {
					color: #fff;
					background-color: #21242B;

					&:not(:first-of-type) {
						font-family: $font-secondary;
					}
				}
			}

			.choose-plan {
				td {
					background-color: transparent;
					padding: 0 !important;

					.choose-btn {
						border: 1px solid #B09762;
						display: block;
						outline: none;
						width: 100%;
						height: 70px;
						font-weight: 600;
						font-size: 18px;
						line-height: 150%;
						background-color: transparent;
						cursor: pointer;
						transition: all ease-in-out 0.4s;

						&.active,
						&:hover {
							background-color: #B09762;
							color: #fff;
						}
					}
				}
			}

			th:not(:first-of-type),
			td:not(:first-of-type) {
				white-space: normal;
				text-align: center;
				width: 130px;
				padding: 10px;
				position: relative;
				border-left: 5px solid #fff;
				position: relative;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					bottom: -11px;
					left: 0;
					right: 0;
					border-left: 1px solid #B09762;
					border-right: 1px solid #B09762;
					opacity: 0;
					visibility: hidden;
					transition: opacity ease-in-out 0.4s;
				}

				&.active,
				&.hover {
					&:before {
						opacity: 1;
						visibility: visible;
					}
				}

				span {
					position: absolute;
					display: block;
					height: 1px;
					width: 16px;
					background-color: #000;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					font-size: 0;
					line-height: 0;

					&.check {
						width: 30px;
						height: 30px;
						border-radius: 50%;
						background-color: #B09762;

						&:before {
							content: '';
							position: absolute;
							top: 45%;
							left: 50%;
							transform: translate(-50%, -50%) rotate(-45deg);
							width: 9px;
							height: 5px;
							border-left: 1px solid #fff;
							border-bottom: 1px solid #fff;
						}
					}
				}
			}
		}
	}

	table {
		width: 1200px;
		border-collapse: collapse;

		@include media('>=992px') {
			width: 100%;
		}
	}

	th {
		text-align: left;
		font-family: $font-secondary;
		font-weight: 600;
		font-size: 18px;
		line-height: 150%;
		color: #fff;
		background-color: #B09762;
		padding: 42px 50px;
		border-collapse: collapse;
		white-space: nowrap;
	}

	td {
		background-color: #F7F6F8;
		padding: 30px 44px;
		border-bottom: 10px solid #fff;
		font-weight: 600;
		font-size: 16px;
		line-height: 150%;
		color: #212121;

		.price {
			font-family: $font-secondary;
			font-size: 22px;
			line-height: 164%;
		}

		.quantity-holder {
			display: flex;
		}

		.minus,
		.plus {
			background: #FFFFFF;
			border: none;
			border-radius: 0;
			height: 60px;
			width: 29px;
			transition: all ease-in-out 0.3s;

			&:hover {
				background-color: #21242B;
				color: #fff;
			}
		}

		.quantity {
			background: #FFFFFF;
			border: none;
			border-radius: 0;
			height: 60px;
			width: 60px;
			text-align: center;
			margin: 0 2px;
		}

		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			/* display: none; <- Crashes Chrome on hover */
			-webkit-appearance: none;
			margin: 0;
			/* <-- Apparently some margin are still there even though it's hidden */
		}
	}
}

.services-plans-text {
	display: block;
	text-align: center;
	font-family: $font-secondary;
	font-weight: 600;
	font-size: 18px;
	line-height: 150%;
	color: #C40401;
	margin: 0 0 50px;
}