.documents-block {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	padding: 30px 20px 42px;
	background-color: #F7F6F8;
	margin-bottom: 10px;
	overflow: hidden;

	@include media('>=tablet') {
		justify-content: space-between;
		padding: 30px 0 30px 42px;
	}

	&:last-of-type {
		margin-bottom: 50px;
	}

	&_not-check {
		justify-content: flex-start;
		align-items: flex-start;

		.documents-title {
			@include media('>=tablet') {
				width: 100%;
				max-width: 940px !important;
			}
		}
	}

	&_payment {
		padding-left: 15px;
		padding-right: 15px;
	}

	.documents-title {

		@include media('>=tablet') {
			max-width: 48%;
		}

		@include media('<991px') {
			margin-bottom: 20px;
			width: 100%;
		}

		strong {
			font-size: 20px;
			font-weight: 500;
			line-height: 23px;
			font-family: $font-secondary;
		}
	}

	.row {
		padding-top: 36px;

		.upload-photo {
			background-color: #fff;
		}
	}

	.btn {
		background-color: #F7F6F8;

		@include media('<768px') {
			margin-bottom: 30px;
		}

		&:hover {
			background-color: #B09762;
		}
	}

	.documents-text-holder {
		padding-top: 15px;
		max-width: 830px;

		p {
			margin: 0;
		}
	}

	.read-holder {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		@include media('>=tablet') {
			flex-direction: row;
		}

		.checkbox-holder {
			position: relative;
			text-align: center;
			width: 250px;

			@include media('>=tablet') {
				margin-left: 43px;
			}
		}

		label {
			position: relative;
			z-index: 2;
			font-weight: 600;
			font-size: 15px;
			line-height: 164%;
			color: #212121;
			transition: color ease-in-out 0.3s;

			&:before {
				background-color: #F7F6F8;
				border: 1px solid #C9C9C9;
				border-radius: 2px;
			}

			&+.pseudo-bg {
				position: absolute;
				top: -20px;
				right: -100vw;
				bottom: -20px;
				left: -100vw;
				z-index: 0;
				transition: all ease-in-out 0.3s;

				@include media('>=768px') {
					right: -20px;
					left: -20px;
				}

				@include media('>=1280px') {
					top: -500px;
					right: 0;
					bottom: -500px;
					left: 0;
				}
			}
		}

		input[type='checkbox']:checked {
			&+label {
				color: #fff;

				&:before {
					border: 1px solid #F1EEF4;
				}

				&+.pseudo-bg {
					background-color: #B09762;
				}
			}
		}
	}

	.img-holder {
		width: 100%;
		max-width: 500px;
		height: auto;

		@include media('>=tablet') {
			margin-right: 40px;
		}
	}
}