.dashboard-title {
	font-family: $font-primary;
	font-weight: 600;
	font-size: 22px;
	line-height: 164%;
	color: #212121;
	margin: 0 0 42px;
	text-transform: uppercase;

	&_top {
		padding-top: 100px;
	}
}

.gallery-section {
	&__holder {
		padding: 30px 15px 10px;
		background-color: #fff;
		margin-bottom: 25px;

		@include media('>=tablet') {
			padding: 50px 35px 10px;
		}

		&_filter {
			padding-bottom: 100px;
		}

		&_service {
			margin-bottom: 100px;
		}
	}

	&__block {
		text-align: center;
	}

	.text-holder {
		text-align: center;
		font-weight: 600;
		font-size: 15px;
		line-height: 150%;
		color: #212121;
		max-width: 992px;
		margin: 0 auto;

		p {
			margin: 0 0 40px;
		}
	}

	.row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -20px;
		justify-content: center;

		@include media('>=tablet') {
			justify-content: flex-start;
		}

		.col {
			width: 100%;
			max-width: 315px;
			padding: 0 20px 40px;
		}
	}

	.upload-photo {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 275px;
		height: 256px;
		background: #F7F6F8;
		border: 2px dashed #E5E4E7;
		font-weight: 600;
		font-size: 15px;
		line-height: 150%;
		color: #212121;
		cursor: pointer;

		&+input[type='file'] {
			opacity: 0;
			position: absolute;
			z-index: -1;
		}

		&:hover {
			.icon-holder {
				box-shadow: 0 4px 10px 6px #A4A3A3;
			}
		}

		.icon-holder {
			position: relative;
			width: 56px;
			height: 56px;
			background-color: #BF0F0D;
			box-shadow: 0 4px 10px 4px rgba(164, 163, 163, 0.25);
			border-radius: 50%;
			margin-bottom: 19px;
			color: #fff;
			font-size: 20px;
			transition: box-shadow ease-in-out 0.3s;

			i {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}

	.img-holder {
		position: relative;
		width: 275px;
		height: 256px;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		background-size: cover;
		margin-bottom: 25px;

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: #2F2F2F;
			opacity: 0;
			transition: opacity ease-in-out 0.3s;
			z-index: 0;
		}

		&:hover {
			&:before {
				opacity: 0.5;
			}

			.gallery-open,
			.remove-img {
				opacity: 1;
				visibility: visible;
			}
		}

		.gallery-open {
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 56px;
			height: 56px;
			background-color: #BF0F0D;
			box-shadow: 0 4px 10px 4px rgba(164, 163, 163, 0.25);
			border-radius: 50%;
			color: #fff;
			opacity: 0;
			visibility: hidden;
			font-size: 27px;
			transition: all ease-in-out 0.3s;
			z-index: 1;

			i {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}

		.remove-img {
			position: absolute;
			bottom: 17px;
			right: 17px;
			background-color: transparent;
			border: none;
			outline: none;
			font-size: 22px;
			color: #fff;
			transition: all ease-in-out 0.3s;
			opacity: 0;
			visibility: hidden;
			z-index: 1;
			cursor: pointer;
		}

		&_video {
			&:before {
				opacity: 0.5;
			}

			.gallery-open {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	strong.img-title {
		font-weight: 500 !important;
		font-size: 15px !important;
		line-height: 150% !important;
		font-family: $font-primary !important;
	}
}

.plan-description-block {
	margin-bottom: 95px;
	
	.plan-description-holder {
		@include media('>=768px') {}
	}

	h2 {
		font-weight: 600;
		font-size: 22px;
		font-family: $font-primary;
		line-height: 34px;
		text-transform: uppercase;
		margin: 0 0 30px;
	}
}